<template>
  <div id="reapplication">
    <transition name="funcList">
      <div v-show="applicationStatus" class="func-operation">
        <div v-for="(item, index) in reimbursement" v-bind:key="index" class="money-item" :class="{'error': item.error, 'active': inputIndex === index}" @click="setValue(index)">
          <p class="left">{{item.name}}</p>
          <input :type="item.type" :placeholder="renderPlace(index)" @input="inputValue(index)" @blur="showValue(index)">
        </div>
        <div id="submit" class="pointer" @click="submit">提交</div>
      </div>
    </transition>
    <transition name="modal">
      <div v-show="inputIndex === 0" class="picker" :style="modalHeight(0)">
        <p>请选择时间</p>
        <div class="date-wrapper">
          <div class="year date pointer" @touchstart="setYear" @touchmove="setYear" @touchend="setYear">
            <div v-for="(i, index) in renderYear" v-bind:key="index" @click="setYear2(index)">
              <p>{{i}}&nbsp;年</p>
            </div>
            <div class="center-line">
            </div>
          </div>
          <div class="month date pointer" @touchstart="setMonth" @touchmove="setMonth" @touchend="setMonth">
            <div v-for="(i, index) in renderMonth" v-bind:key="index" @click="setMonth2(index)">
              <p>{{i}}&nbsp;月</p>
            </div>
            <div class="center-line">
            </div>
          </div>
          <div class="day date pointer" @touchstart="setDay" @touchmove="setDay" @touchend="setDay">
            <div v-for="(i, index) in renderDay" v-bind:key="index" @click="setDay2(index)">
              <p>{{i}}&nbsp;日</p>
            </div>
            <div class="center-line">
            </div>
          </div>
        </div>
        <div class="choices">
          <div id="date-cancel" @click="cancelInput">取消</div>
          <div id="date-submit" @click="input">确定</div>
        </div>
      </div>
    </transition>
    <transition name="modal">
      <div v-show="inputIndex === 1" class="picker" :style="modalHeight(1)">
        <p>请选择类型</p>
        <div class="type-wrapper">
          <div class="type pointer" @touchstart="setType" @touchmove="setType" @touchend="setType">
            <div v-for="(item, index) in renderType" v-bind:key="index" @click="setType2(index)">
              <p>{{item}}</p>
            </div>
            <div class="center-line">
            </div>
          </div>
        </div>
        <div class="choices">
          <div id="type-cancel" @click="cancelInput">取消</div>
          <div id="type-submit" @click="input">确定</div>
        </div>
      </div>
    </transition>
  </div>
</template>
<!--#757575-->
<script>
import $ from 'jquery'
export default {
  name: 're-application',
  data () {
    return {
      applicationStatus: false,
      reimbursement: [
        {name: '起止日期', id: 'expense_date', type: 'text', error: false},
        {name: '费用类型', id: 'expense_type', type: 'text', error: false},
        {name: '费用描述', id: 'expense_description', type: 'text', error: false},
        {name: '发票索引', id: 'receipt_reference', type: 'text', error: false},
        {name: '发票金额', id: 'receipt_amount', type: 'text', error: false}
      ],
      inputIndex: -1,
      year: 0,
      month: 0,
      day: 0,
      tempYear: 0,
      tempMonth: 0,
      tempDay: 0,
      time: 0,
      posY: 0,
      typeList: [
        '市内交通费',
        '在途费用',
        '差旅补贴'
      ],
      typeIndex: -1,
      tempTypeIndex: -1,
      number: 0
    }
  },
  methods: {
    application: function () {
      $.ajax({
        async: false,
        url: '/expense_application/',
        type: 'POST',
        data: {expense_date: $('#expense_date').val(),
          expense_description: $('#expense_description').val(),
          expense_type: $('#expense_type').val(),
          receipt_reference: $('#receipt_reference').val(),
          receipt_currency: 'CNY',
          receipt_amount: $('#receipt_amount').val(),
          exchange_rate: 1.0000,
          converted_amount: $('#converted_amount').val(),
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token},
        success: function (data) {
          alert(data)
        }
      })
    },
    setValue: function (i) {
      this.inputIndex = i
      if (i > 1) {
        $('.money-item input').eq(i).focus()
      }
      var height
      switch (i) {
        case 0:
          $('.money-item input').eq(i).blur()
          this.tempYear = this.year
          this.tempMonth = this.month
          this.tempDay = this.day
          this.$root.eventHub.$emit('prompt')
          break
        case 1:
          $('.money-item input').eq(i).blur()
          if ($('.money-item input').eq(1).val() === '') {
            this.typeIndex = 1
          }
          this.tempTypeIndex = this.typeIndex
          this.$root.eventHub.$emit('prompt')
          break
        case 2:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 3
            $('#app').animate({scrollTop: height}, 400)
          }
          break
        case 3:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 4
            $('#app').animate({scrollTop: height}, 400)
          }
          break
        case 4:
          if (!this.$root.adapt) {
            height = $('#main').height() + 58 * 5
            $('#app').animate({scrollTop: height}, 400)
          }
          if (event.target.value !== '') {
            $('.money-item input').eq(4).val(this.number)
          }
          break
      }
    },
    setYear: function (e) {
      e.preventDefault()
      e.stopPropagation()
      if (!e) {
        e = window.event
      }
      switch (e.type) {
        case 'touchstart':
          this.posY = e.targetTouches[0].pageY
          break
        case 'touchmove':
          var temp = e.targetTouches[0].pageY - this.posY
          var gap = 30
          if (temp > gap || temp < -gap) {
            if ((this.tempYear % 4 === 0 && this.tempYear % 100 !== 0) || this.tempYear % 400 === 0) {
              if (this.tempMonth === 2 && this.tempDay === 29) {
                this.tempDay = 28
              }
            }
            if (temp > gap) {
              this.posY = e.targetTouches[0].pageY
              this.tempYear--
            } else if (temp < -gap) {
              this.posY = e.targetTouches[0].pageY
              this.tempYear++
            }
          }
          break
        case 'touchend':
          // eslint-disable-next-line no-redeclare
          // var time = new Date()
          // var speed = (e.changedTouches[0].pageY - this.posY) * 1000 / (time.getTime() - this.tempTime)
          // var duration = 0.3
          // var gap = 20
          // // eslint-disable-next-line no-redeclare
          // var temp = speed / (duration * 10)
          // console.log(this.tempYear)
          // var self = this
          // for (var i = 0; i < duration; i += 0.1) {
          //   setTimeout(function () {
          //     speed -= temp
          //     if (speed > -1 && temp < 1) {
          //       speed = 0
          //     }
          //     console.log(speed)
          //     if (speed > gap || speed < -gap) {
          //       if ((self.tempYear % 4 === 0 && self.tempYear % 100 !== 0) || self.tempYear % 400 === 0) {
          //         if (self.tempMonth === 2 && self.tempDay === 29) {
          //           self.tempDay = 28
          //         }
          //       }
          //       if (speed > gap) {
          //         self.tempYear -= parseInt((speed / gap).toFixed())
          //       }
          //       console.log(self.tempYear)
          //     }
          //   }, i * 1000)
          // }
          break
      }
    },
    setYear2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            if ((this.tempYear % 4 === 0 && this.tempYear % 100 !== 0) || this.tempYear % 400 === 0) {
              if (this.tempMonth === 2 && this.tempDay === 29) {
                this.tempDay = 28
              }
            }
            this.tempYear--
            break
          case 2:
            if ((this.tempYear % 4 === 0 && this.tempYear % 100 !== 0) || this.tempYear % 400 === 0) {
              if (this.tempMonth === 2 && this.tempDay === 29) {
                this.tempDay = 28
              }
            }
            this.tempYear++
            break
        }
      }
    },
    setMonth: function (e) {
      e.preventDefault()
      e.stopPropagation()
      if (!e) {
        e = window.event
      }
      switch (e.type) {
        case 'touchstart':
          this.posY = e.targetTouches[0].pageY
          break
        case 'touchmove':
          var temp = e.targetTouches[0].pageY - this.posY
          var gap = 30
          if (temp > gap) {
            this.posY = e.targetTouches[0].pageY
            this.tempMonth--
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 0) {
              this.tempYear--
              this.tempMonth = 12
            }
          } else if (temp < -gap) {
            this.posY = e.targetTouches[0].pageY
            this.tempMonth++
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 13) {
              if (this.tempDay === this.getDaysInMonth(this.year, 12)) {
                this.tempDay = 1
              }
              this.tempYear++
              this.tempMonth = 1
            }
          }
          break
        case 'touchend':
          break
      }
    },
    setMonth2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            this.tempMonth--
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 0) {
              this.tempYear--
              this.tempMonth = 12
            }
            break
          case 2:
            this.tempMonth++
            if (this.tempDay > this.getDaysInMonth(this.year, this.tempMonth)) {
              this.tempDay = this.getDaysInMonth(this.year, this.tempMonth)
            }
            if (this.tempMonth === 13) {
              if (this.tempDay === this.getDaysInMonth(this.year, 12)) {
                this.tempDay = 1
              }
              this.tempYear++
              this.tempMonth = 1
            }
            break
        }
      }
    },
    setDay: function (e) {
      e.preventDefault()
      e.stopPropagation()
      if (!e) {
        e = window.event
      }
      switch (e.type) {
        case 'touchstart':
          this.posY = e.targetTouches[0].pageY
          break
        case 'touchmove':
          var temp = e.targetTouches[0].pageY - this.posY
          var gap = 30
          if (temp > gap) {
            this.posY = e.targetTouches[0].pageY
            this.tempDay--
            if (this.tempDay === 0) {
              if (this.tempMonth === 1) {
                this.tempYear--
                this.tempMonth = 12
              } else {
                this.tempMonth--
              }
              this.tempDay = this.getDaysInMonth(this.tempYear, this.tempMonth)
            }
          } else if (temp < -gap) {
            this.posY = e.targetTouches[0].pageY
            this.tempDay++
            if (this.tempDay > this.getDaysInMonth(this.tempYear, this.tempMonth)) {
              this.tempDay = 1
              if (this.tempMonth === 12) {
                this.tempYear++
                this.tempMonth = 1
              } else {
                this.tempMonth++
              }
            }
          }
          break
        case 'touchend':
          break
      }
    },
    setDay2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            this.tempDay--
            if (this.tempDay === 0) {
              if (this.tempMonth === 1) {
                this.tempYear--
                this.tempMonth = 12
              } else {
                this.tempMonth--
              }
              this.tempDay = this.getDaysInMonth(this.tempYear, this.tempMonth)
            }
            break
          case 2:
            this.tempDay++
            if (this.tempDay > this.getDaysInMonth(this.tempYear, this.tempMonth)) {
              this.tempDay = 1
              if (this.tempMonth === 12) {
                this.tempYear++
                this.tempMonth = 1
              } else {
                this.tempMonth++
              }
            }
            break
        }
      }
    },
    setType: function (e) {
      e.preventDefault()
      e.stopPropagation()
      if (!e) {
        e = window.event
      }
      switch (e.type) {
        case 'touchstart':
          this.posY = e.targetTouches[0].pageY
          break
        case 'touchmove':
          var temp = e.targetTouches[0].pageY - this.posY
          if (temp > 40) {
            this.posY = e.targetTouches[0].pageY
            if (this.tempTypeIndex > 0) {
              this.tempTypeIndex--
            } else {
              this.tempTypeIndex = 0
            }
          } else if (temp < -40) {
            this.posY = e.targetTouches[0].pageY
            if (this.tempTypeIndex < 2) {
              this.tempTypeIndex++
            } else {
              this.tempTypeIndex = 2
            }
          }
          break
        case 'touchend':
          break
      }
    },
    setType2: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            if (this.tempTypeIndex > 0) {
              this.tempTypeIndex--
            }
            break
          case 2:
            if (this.tempTypeIndex < 2) {
              this.tempTypeIndex++
            }
            break
        }
      }
    },
    inputValue: function (i) {
      switch (i) {
        case 4:
          this.number = event.target.value
          break
      }
    },
    showValue: function (i) {
      if (event.target.value !== '') {
        this.reimbursement[i].error = false
      }
      switch (i) {
        case 2:
        case 3:
          this.inputIndex = -1
          break
        case 4:
          this.inputIndex = -1
          if (event.target.value === '') {
            this.number = 0
          }
          event.target.value = this.number + ' 元'
          break
      }
    },
    getCurrentDate: function () {
      var d = new Date()
      this.year = d.getFullYear()
      this.month = d.getMonth() + 1
      this.day = d.getDate()
    },
    getDaysInMonth: function (year, month) {
      var temp = new Date(year, month, 0)
      return temp.getDate()
    },
    input: function () {
      this.reimbursement[this.inputIndex].error = false
      switch (this.inputIndex) {
        case 0:
          this.year = this.tempYear
          this.month = this.tempMonth
          this.day = this.tempDay
          $('.money-item input').eq(0).val(this.year + '年' + this.month + '月' + this.day + '日')
          break
        case 1:
          this.typeIndex = this.tempTypeIndex
          $('.money-item input').eq(1).val(this.typeList[this.typeIndex])
      }
      this.$root.eventHub.$emit('prompt')
      this.hideModal()
    },
    cancelInput: function () {
      this.$root.eventHub.$emit('prompt')
      this.hideModal()
    },
    hideModal: function () {
      this.inputIndex = -1
    },
    modalHeight: function (i) {
      var height
      switch (i) {
        case 0:
        case 1:
          height = 265
          break
      }
      var str = 'height: ' + height + 'px'
      return str
    },
    renderPlace: function (i) {
      switch (i) {
        case 0:
          return '请输入活动日期'
        case 1:
          return '请选择费用类型'
        case 2:
          return '必填'
        case 3:
          return '必填'
        case 4:
          return '例: 30元'
      }
    },
    submit: function () {
      var tag = true
      var input = $('.money-item input')
      for (var i = 0; i < input.length; i++) {
        if (input[i].value === '') {
          this.reimbursement[i].error = true
          tag = false
        }
      }
      if (tag) {
        var date = this.year + '.' + this.month + '.' + this.day
        var amount = (this.number * 1.0000).toFixed(2)
        var self = this
        $.ajax({
          async: false,
          url: '/expense_application/',
          type: 'POST',
          data: {
            expense_date: date,
            expense_description: $('.money-item input').eq(2).val(),
            expense_type: $('.money-item input').eq(1).val(),
            receipt_reference: $('.money-item input').eq(3).val(),
            receipt_currency: 'CNY',
            receipt_amount: amount,
            exchange_rate: 1.0000,
            converted_amount: amount,
            // eslint-disable-next-line no-undef
            csrfmiddlewaretoken: csrf_token
          },
          success: function (data) {
            var mode
            if (data === 'success') {
              mode = {
                text: '成功提交报销申请！',
                duration: 1000
              }
              self.$root.eventHub.$emit('showPrompt', mode)
              self.$router.push({path: '/user/reimbursement'})
            } else {
              mode = {
                text: '提交失败！',
                duration: 1000
              }
              self.$root.eventHub.$emit('showPrompt', mode)
            }
          }
        })
      } else {
        var mode = {
          text: '请输入完整的报销信息！',
          duration: 1200
        }
        this.$root.eventHub.$emit('showPrompt', mode)
      }
    }
  },
  computed: {
    renderYear: function () {
      var year = []
      for (var i = this.tempYear - 1; i <= this.tempYear + 1; i++) {
        year.push(i)
      }
      return year
    },
    renderMonth: function () {
      var month = []
      for (var i = this.tempMonth - 1; i <= this.tempMonth + 1; i++) {
        if (i === 0) {
          month.push(12)
        } else if (i === 13) {
          month.push(1)
        } else {
          month.push(i)
        }
      }
      return month
    },
    renderDay: function () {
      var day = []
      for (var i = this.tempDay - 1; i <= this.tempDay + 1; i++) {
        if (i === 0) {
          if (this.tempMonth === 1) {
            day.push(this.getDaysInMonth(this.tempYear - 1, 12))
          } else {
            day.push(this.getDaysInMonth(this.tempYear, this.tempMonth - 1))
          }
        } else if (i > this.getDaysInMonth(this.tempYear, this.tempMonth)) {
          day.push(1)
        } else {
          day.push(i)
        }
      }
      return day
    },
    renderType: function () {
      var type = []
      var i
      switch (this.tempTypeIndex) {
        case 0:
          type.push('')
          type.push(this.typeList[0])
          type.push(this.typeList[1])
          break
        case 1:
          for (i = 0; i < 3; i++) {
            type.push(this.typeList[i])
          }
          break
        case 2:
          type.push(this.typeList[1])
          type.push(this.typeList[2])
          type.push('')
          break
      }
      return type
    }
  },
  mounted () {
    var self = this
    this.applicationStatus = true
    this.getCurrentDate()
    this.$root.eventHub.$on('cancelPrompt', function () {
      if (self.inputIndex !== -1) {
        self.hideModal()
      }
    })
  }
}
</script>

