    user-select: none;
    cursor: pointer;
  }
  #date-cancel, #type-cancel{
    margin-left: calc((100% - 270px) / 2);
    background: #dc143c;
  }
  #date-submit, #type-submit{
    margin-left: 30px;
    background: #3cb371;
  }
  #submit{
    text-align: center;
    color: white;
    margin-bottom: 50px;
    background: #3cb371;
    border-radius: 3px;
    user-select: none;
    -webkit-transition: background-color ease-out .2s;
    -moz-transition: background-color ease-out .2s;
    -o-transition: background-color ease-out .2s;
    transition: background-color ease-out .2s;
  }
  #submit:active{
    background: #2EA057;
  }
  .modal-enter-active{
    -webkit-transition: all .25s;
    -o-transition: all .25s;
    transition: all .25s;
  }
  .modal-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
    -o-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation {
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .money-item p{
      font-size: 17px;
      height: 41px;
      line-height: 41px;
      margin: 8px 0;
    }
    .money-item input{
      font-size: 17px;
      height: 41px;
      line-height: 41px;
      margin: 8px 0;
      width: calc(100% - 80px);
    }
    .money-item.error p, .money-item.error input, .money-item.active p, .money-item.active input{
      margin: 8px 0 7px;
    }
    .money-item:first-of-type input, .money-item:nth-of-type(2) input{
      color: transparent;
      text-shadow: 0 0 0 #000000;
    }
    .money-item:nth-of-type(3) input::-moz-selection, .money-item:nth-of-type(5) input::-moz-selection{
      color: white;
      background-color: #3cb371;
    }
    .money-item:nth-of-type(3) input::selection, .money-item:nth-of-type(5) input::selection{
      color: white;
      background-color: #3cb371;
    }
    .picker{
      width: 100%;
      bottom: 0;
      left: 0;
    }
    .picker>p:first-of-type, .date p, .type p, #date-submit, #type-submit, #date-cancel, #type-cancel{
      font-size: 17px;
    }
    #submit{
      width: 100%;
      height: 42px;
      line-height: 42px;
      margin: 20px 0 50px;
      font-size: 17px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation {
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .money-item{
      width: calc(100% - 40px);
      padding: 10px 20px;
    }
    .money-item p{
      font-size: 18px;
      height: 39px;
      line-height: 39px;
    }
    .money-item input{
      font-size: 18px;
      height: 39px;
      line-height: 39px;
      width: calc(100% - 100px);
    }
    .money-item.error p, .money-item input, .money-item.active p, .money-item.active input{
      height: 38px;
    }
    .picker{
      width: 396px;
      height: 300px;
      left: calc((100% - 396px) / 2);
      top: calc((100% - 300px) / 2);
    }
    .picker>p:first-of-type, .date p, .type p, #date-submit, #type-submit, #date-cancel, #type-cancel{
      font-size: 17px;
    }
    #submit{
      width: 200px;
      height: 42px;
      margin: 30px calc((100% - 200px) / 2) 10px;
      font-size: 18px;
      line-height: 42px;
    }
  }
</style>



